#skills {
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
   text-align: center;
   margin: 0 auto;
}
.skillTitle {
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;

}
.skillDesc {
    font-weight:400 ;
    font-size: 1rem;
    max-width: 50rem;
    padding: 0 2rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.skillBars {
    margin: 1.5rem;
    width: 100vw;
    max-width: 80%;
    text-align: left;

}
.skillBar {
    display: flex;
    margin: 1rem;
    padding: 1.5rem 2rem;
    border-radius: 0.5rem;
    background: rgb(50,50,50);
}

.skillBarImg {
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin-right: 2rem;
}
.skillBartext p {
  font-size: 1rem;
  font-weight: 400;
  margin-top: 0.3rem;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  
}
.skillBartext h2 {
    color: yellow;
}
@media screen and (max-width:720px) {
     .skillBartext>p {
        font-size: 2vw;
     }
}
@media screen and (max-width:480px) {
    .skillBartext>p {
       font-size: 3vw;
    }
    .skillBarImg {
        height: 2.25rem;
        width: 2.25rem;
    }
    .skillBartext>h2 {
        font-size: 5vw;
    }
}
